
export default {
    name: "ListItem",
    props: {
        place: {
            type: String,
            required: true
        },
        itemTitle: {
            type: String,
            required: true
        },
        date: {
            type: String,
            required: true
        },
        description: {
            type: String,
            default: "",
        },
        imageName: {
            type: String,
            default: "",
        }
    },
    computed: {
        hasImage() {
            return this.imageName !== "";
        }
    },
    data() {
        return {
            publicPath: process.env.BASE_URL,
        };
    }
};
